@import "https://fonts.googleapis.com/icon?family=Material+Icons";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #f4f4f4;
  font-family: Arial, Helvetica, sans-serif;
}

img {
  object-fit: cover;
  width: 100%;
}

.carousel {
  background-color: #adbc9f;
  padding: 20px;
}

.carousel-inner {
  height: 300px !important;
}

.app {
  font-family: Roboto, sans-serif !important;
}

.container-fluid .logo img {
  width: 13vw;
}

.card-img-top {
  object-fit: cover;
  width: 100%;
  height: 14vw;
}

.w-100 {
  height: 575px;
  width: 100% !important;
}

.btn-primary {
  margin-right: 25px;
}

.navbar {
  z-index: 50;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1.5rem 1rem;
  display: flex;
  background-color: #12372a !important;
}

.navbar__logo h2 {
  color: #fbfada;
  cursor: pointer;
  font-size: 1.4rem;
}

.navbar__links {
  align-items: center;
  list-style: none;
  display: flex;
}

ul {
  margin-bottom: 0 !important;
}

.navbar__links > li {
  padding-left: 1.5rem;
}

.navbar__links > li > a {
  color: #fbfada;
  align-items: center;
  font-size: 1.2rem;
  text-decoration: none;
  display: flex;
}

.navbar__links > li > a > span {
  align-items: center;
  margin-left: 8px;
  display: flex;
}

.cart__link {
  background: #436850;
  border: 2px solid #436850;
  border-radius: 8px;
  padding: 8px;
}

.cart__link:hover {
  color: #fbfada;
  background: #12372a;
  border: 2px solid #436850;
}

.cartlogo__badge {
  color: #12372a;
  background-color: #fbfada;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin-left: 8px;
  font-size: 1rem;
  display: flex;
}

.hamburger__menu {
  cursor: pointer;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 30px;
  height: 30px;
  display: none;
}

.hamburger__menu:hover > div {
  background: #fbfada;
}

.hamburger__menu > div {
  background: #fbfada;
  width: 100%;
  height: 3px;
}

@media (width <= 960px) {
  .navbar__links {
    display: none;
  }

  .hamburger__menu {
    display: flex;
  }
}

@media (width <= 500px) {
  .navbar__logo h2 {
    font-size: 1rem;
  }
}

.sidedrawer {
  z-index: 200;
  background: #fbfada;
  flex-direction: column;
  justify-content: center;
  width: 70%;
  height: 100vh;
  transition: all .3s ease-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.sidedrawer.show {
  transform: translateX(0);
}

.sidedrawer__links {
  flex-direction: column;
  list-style: none;
  display: flex;
}

.sidedrawer__links > li {
  align-items: center;
  display: flex;
}

.sidedrawer__links > li > a:hover {
  color: #f4f4f4;
  background: #436850;
}

.sidedrawer__links > li > a {
  text-align: center;
  color: #12372a;
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  font-size: 1.6rem;
  text-decoration: none;
  display: flex;
}

.sidedrawer__links > li > a > span {
  color: #12372a;
  align-items: center;
  margin-left: 8px;
  display: flex;
}

.sidedrawer__cartbadge {
  color: #fff;
  background-color: #12372a;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin-left: 8px;
  font-size: 1rem;
  display: flex;
}

.sidedrawer__links > li > a:hover .sidedrawer__cartbadge {
  color: #12372a;
  background-color: #fff;
}

@media (width >= 960px) {
  .sidedrawer {
    display: none;
  }
}

.backdrop {
  z-index: 100;
  background: #00000080;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

.footer-dark {
  color: #fbfada;
  margin-top: 80px;
  padding: 50px 0;
  background-color: #12372a !important;
}

.footer-dark h3 {
  margin-top: 0;
  margin-bottom: 12px;
  font-size: 16px;
  font-weight: bold;
}

.footer-dark ul {
  margin-bottom: 0;
  padding: 0;
  font-size: 14px;
  line-height: 1.6;
  list-style: none;
}

.footer-dark ul a {
  color: inherit;
  opacity: .6;
  text-decoration: none;
}

.footer-dark ul a:hover {
  opacity: .8;
}

@media (width <= 767px) {
  .footer-dark .item:not(.social) {
    text-align: center;
    padding-bottom: 20px;
  }
}

.footer-dark .item.text {
  margin-bottom: 36px;
}

@media (width <= 767px) {
  .footer-dark .item.text {
    margin-bottom: 0;
  }
}

.footer-dark .item.text p {
  opacity: .6;
  margin-bottom: 0;
}

.footer-dark .copyright {
  text-align: center;
  opacity: .3;
  margin-bottom: 0;
  padding-top: 24px;
  font-size: 13px;
}

.homescreen {
  max-width: 1300px;
  margin: 1rem auto;
}

.homescreen__title {
  color: #171717;
  margin-bottom: 1rem;
  margin-left: 8px;
  font-size: 1.5rem;
}

.homescreen__products {
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

@media (width <= 1232px) {
  .homescreen__products {
    grid-template-columns: repeat(3, auto);
  }
}

@media (width <= 950px) {
  .homescreen__products {
    grid-template-columns: repeat(2, auto);
  }
}

@media (width <= 630px) {
  .homescreen__products {
    grid-template-columns: 1fr;
  }
}

.product {
  cursor: pointer;
  background: #fff;
  border-radius: 8px;
  flex-direction: column;
  justify-content: space-between;
  width: 370px;
  margin: 15px auto;
  padding: 1rem;
  display: flex;
  box-shadow: 0 1px 4px #0006;
}

.product > img {
  object-fit: contain;
  border-radius: 8px;
  width: 100%;
  height: 170px;
}

.product__info > p {
  margin-bottom: 8px;
}

.info__price {
  font-weight: bold;
}

.info__name {
  font-size: 1rem;
  overflow: hidden;
}

.info__description {
  font-size: .8rem;
}

.info__button {
  text-align: center;
  color: #fff;
  background-color: #436850;
  border: 1px solid #436850;
  border-radius: 8px;
  width: 100%;
  padding: 8px 16px;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  display: block;
}

.info__button:hover {
  color: #fff;
  background: #12372a;
}

@media (width <= 1232px) {
  .product {
    width: 360px;
  }
}

@media (width <= 1115px) {
  .product {
    width: 330px;
  }
}

@media (width <= 1028px) {
  .product {
    width: 300px;
  }
}

@media (width <= 950px) {
  .product {
    width: 400px;
  }
}

@media (width <= 830px) {
  .product {
    width: 330px;
  }
}

@media (width <= 700px) {
  .product {
    width: 290px;
  }
}

@media (width <= 630px) {
  .product {
    width: 450px;
  }
}

@media (width <= 500px) {
  .product {
    width: 350px;
  }
}

@media (width <= 400px) {
  .product {
    width: 300px;
  }
}

.productscreen {
  max-width: 1300px;
  margin: 1rem auto;
  display: flex;
}

.productscreen__left {
  flex: .8;
  display: flex;
}

.left__image {
  flex: .6;
  margin: 1rem;
}

.left__info {
  background: #fff;
  flex: .4;
  height: fit-content;
  margin: 1rem;
  font-size: .9rem;
}

.left__name {
  font-size: 1.3rem;
  font-weight: bold;
}

.left__info > p, .right__info > p {
  border-bottom: 1px solid #0003;
  padding: 1rem;
}

.left__info > p:last-child, .right__info > p:last-child {
  border: none;
}

.productscreen__right {
  flex: .2;
}

.right__info {
  background: #fff;
  width: 250px;
  margin: 1rem;
  box-shadow: 0 1px 4px #0006;
}

.right__info p {
  grid-template-columns: repeat(2, 1fr);
  padding: 1rem;
  font-size: .8rem;
  display: grid;
}

.right__info p > button {
  color: #f4f4f4;
  cursor: pointer;
  background: #436850;
  border: 1px solid #436850;
  border-radius: 5px;
  grid-column: 1 / -1;
  width: 100%;
  padding: 10px 16px;
}

.right__info select {
  padding: 10px 16px;
}

@media (width <= 960px) {
  .productscreen {
    flex-direction: column;
  }

  .productscreen__left {
    flex-direction: column;
    flex: 1;
  }

  .left__image, .left__info {
    flex: 1;
  }

  .productscreen__right {
    flex: 1;
    padding: 1rem;
  }

  .right__info {
    width: 100%;
    margin: 0;
  }
}

.cartscreen {
  max-width: 1300px;
  margin: 2rem auto;
  display: flex;
}

.cartscreen h2 {
  margin-bottom: 1rem;
}

.cartscreen__left {
  background: none;
  flex: .7;
  margin-right: 1rem;
  padding: 1rem;
}

.cartscreen__right {
  background: #fff;
  flex: .3;
  height: fit-content;
  box-shadow: 0 1px 4px #0006;
}

.cartscreen__right div {
  border-bottom: 1px solid #0003;
  padding: 1rem;
}

.cartscreen__info p {
  padding: 8px;
}

.cartscreen__info p:first-child {
  font-weight: bold;
}

.cartscreen__right div:last-child {
  border: none;
}

.cartscreen__right div button {
  color: #f4f4f4;
  cursor: pointer;
  background: #171717;
  border: 1px solid #171717;
  width: 100%;
  padding: 10px 17px;
}

.cartscreen__right div button:hover {
  opacity: .9;
}

@media (width <= 1320px) {
  .cartscreen {
    max-width: 900px;
  }
}

@media (width <= 960px) {
  .cartscreen {
    flex-direction: column;
    max-width: 800px;
  }

  .cartscreen__right {
    margin: 1rem;
  }
}

.cartitem {
  background: #fff;
  border-radius: 2px;
  grid-template-columns: 1fr 4fr 1fr 1fr 1fr;
  place-items: center;
  gap: 8px;
  width: 100%;
  margin-bottom: 8px;
  padding: 1rem;
  display: grid;
}

.cartItem__name {
  color: #171717;
  text-decoration: none;
}

.cartItem__name:hover {
  color: #dd219e;
}

.cartItem__select {
  padding: 10px 17px;
}

.cartItem__deleteBtn {
  color: red;
  cursor: pointer;
  background: #f4f4f4;
  border: 1px solid #171717;
  padding: 10px 17px;
  transition: all .3s ease-out;
}

.cartItem__deleteBtn:hover, .carItem__deleteBtn:active, .carItem__deleteBtn:focus {
  background: #171717;
  transform: scale(1.2);
}

@media (width <= 700px) {
  .cartItem__name {
    font-size: .6rem;
  }

  .cartItem__select, .cartItem__deleteBtn {
    padding: 5px 8px;
  }

  .cartitem__price {
    font-size: .6rem;
  }
}

* {
  font-family: Roboto, sans-serif;
}

